<template>
    <div class="register-container" :style="{ backgroundImage: `url(${backgroundImage})` }">
        <el-card class="register-card">
            <h2 class="register-title">AI Art Generator</h2>
            <el-form :model="registerForm" :rules="registerRules" ref="registerForm">
                <el-form-item prop="phone">
                    <el-input v-model="registerForm.phone" placeholder="请输入手机号"></el-input>
                </el-form-item>
                <el-form-item prop="code">
                    <el-input v-model="registerForm.code" placeholder="请输入验证码">
                        <template #append>
                            <el-button type="primary" :disabled="isCodeSending" @click="sendCode">
                                {{ codeButtonText }}
                            </el-button>
                        </template>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input type="password" v-model="registerForm.password" placeholder="请输入密码"
                        show-password></el-input>
                </el-form-item>
                <el-form-item prop="confirmPassword">
                    <el-input type="password" v-model="registerForm.confirmPassword" placeholder="请确认密码" show-password
                        @blur="validateConfirmPasswordOnBlur"></el-input>
                </el-form-item>
                <el-button type="primary" @click="submitForm('registerForm')" class="register-button">注册</el-button>
                <p class="login-link">
                    已有账号？<router-link to="/login">立即登录</router-link>
                </p>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { phoneRules, passwordRules, codeRules, validateConfirmPassword } from '@/utils/validationRules';

export default {
    data() {
        return {
            backgroundImage: require('@/assets/ai-art-background.png'),
            registerForm: {
                phone: '',
                code: '',
                password: '',
                confirmPassword: ''
            },
            isCodeSending: false,
            codeButtonText: '获取验证码',
            registerRules: {
                phone: phoneRules,
                code: codeRules,
                password: passwordRules,
                confirmPassword: [
                    { required: true, message: '请确认密码', trigger: 'blur' },
                    { validator: (rule, value, callback) => validateConfirmPassword(rule, value, callback, this.registerForm) }
                ]
            }
        };
    },
    methods: {
        // 提交注册表单
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$message({
                        message: '注册成功',
                        type: 'success'
                    });
                    this.$router.push('/login');
                } else {
                    this.$message({
                        message: '表单验证失败',
                        type: 'error'
                    });
                }
            });
        },
        // 发送验证码
        sendCode() {
            this.isCodeSending = true;
            this.codeButtonText = '发送中...';
            setTimeout(() => {
                this.isCodeSending = false;
            }, 3000);
        },
        // 在 blur 事件中手动触发确认密码验证
        validateConfirmPasswordOnBlur() {
            this.$refs.registerForm.validateField('confirmPassword', (errorMessage) => {
                if (errorMessage) {
                    this.$message({
                        message: errorMessage,
                        type: 'error'
                    });
                }
            });
        }
    }
};
</script>

<style scoped>
.register-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
}

.register-card {
    width: 400px;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.register-title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
}

.register-button {
    width: 100%;
    margin-top: 20px;
}

.login-link {
    text-align: center;
    margin-top: 20px;
}

.login-link a {
    color: #409EFF;
    text-decoration: none;
}
</style>