// src/utils/validationRules.js

// 手机号验证规则
export const phoneRules = [
    { required: true, message: '请输入手机号', trigger: 'blur' },
    { pattern: /^1[3-9]\d{9}$/, message: '请输入合法的手机号', trigger: 'blur' }
];

// 密码验证规则
export const passwordRules = [
    { required: true, message: '请输入密码', trigger: 'blur' },
    { validator: validatePassword, trigger: 'blur' }
];

// 验证码验证规则
export const codeRules = [
    { required: true, message: '请输入验证码', trigger: 'blur' }
];

// 密码验证逻辑
export function validatePassword(rule, value, callback) {
    if (value.length < 8 || value.length > 16) {
        callback(new Error('密码长度必须在8到16位之间'));
    } else {
        // 检查密码是否包含至少两种类型的字符
        const checks = [
            /[a-z]/.test(value), // 小写字母
            /[A-Z]/.test(value), // 大写字母
            /\d/.test(value),   // 数字
            /[^a-zA-Z0-9]/.test(value) // 特殊字符
        ];
        const valid = checks.filter(Boolean).length >= 2; // 至少满足两种
        if (!valid) {
            callback(new Error('密码至少包含小写字母、大写字母、数字、特殊字符中的任意两种'));
        } else {
            callback();
        }
    }
}

// 确认密码验证逻辑
export function validateConfirmPassword(rule, value, callback, form) {
    if (value !== form.password) {
        callback(new Error('两次输入的密码不一致！'));
    } else {
        callback();
    }
}